/* ========= GENERAL ========= */
body {
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #e0f7fa 0%, #f0f4f8 100%);
  margin: 0;
  padding: 2rem;
  display: flex;
  justify-content: center;
  color: #1e293b;
}

/* ========= TITRES ========= */
h1, h2, h3 {
  font-weight: 700;
  text-align: center;
  opacity: 0;
  animation: fadeInSlide 1.2s ease-in-out forwards;
}
h1 {
  color: #0f766e;
  font-size: 2.4rem;
  margin-bottom: 2rem;
}
h2 {
  font-size: 2rem;
  color: #1d4ed8;
  border-bottom: 3px solid #60a5fa;
  padding-bottom: 0.5rem;
}
h3 {
  font-size: 1.4rem;
  color: #0f172a;
  border-bottom: 2px dashed #cbd5e1;
  padding-bottom: 0.4rem;
  margin-bottom: 1rem;
}

/* ========= CONTAINER ========= */
.container {
  width: 100%;
  max-width: 960px;
  opacity: 0;
  animation: fadeInSlide 1s ease-in-out forwards;
}

/* ========= ANIMATIONS ========= */
@keyframes fadeInSlide {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes glowInput {
  0% { box-shadow: 0 0 0 0 rgba(37, 99, 235, 0); }
  100% { box-shadow: 0 0 10px rgba(37, 99, 235, 0.4); }
}

/* ========= FORMULAIRE ========= */
form {
  background-color: #ffffff;
  border: 1.5px solid #e2e8f0;
  padding: 2rem 2.5rem;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.06);
  margin-bottom: 2.5rem;
  opacity: 0;
  animation: fadeInSlide 1.1s ease-in-out forwards;
  transition: box-shadow 0.3s ease;
}
form:hover {
  box-shadow: 0 20px 45px rgba(0, 0, 0, 0.08);
}

form label {
  display: block;
  margin-top: 1.2rem;
  margin-bottom: 0.4rem;
  font-weight: 600;
  color: #475569;
}

form input,
form select {
  width: 100%;
  padding: 0.85rem 1rem;
  font-size: 1rem;
  border: 2px solid #cbd5e1;
  border-radius: 12px;
  background-color: #f9fafb;
  transition: all 0.3s ease;
  box-sizing: border-box;
}
form input:hover,
form select:hover {
  background-color: #f1f5f9;
}
form input:focus,
form select:focus {
  border-color: #3b82f6;
  outline: none;
  animation: glowInput 0.4s ease forwards;
}

/* ========= BOUTONS FORMULAIRE ========= */
form button {
  margin-top: 1.5rem;
  margin-right: 0.5rem;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  font-size: 1rem;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}
form button[type="submit"] {
  background: linear-gradient(to right, #2563eb, #1d4ed8);
  color: white;
}
form button[type="submit"]:hover {
  background: #1e40af;
  transform: scale(1.05);
}
form button[type="button"] {
  background-color: #64748b;
  color: white;
}
form button[type="button"]:hover {
  background-color: #475569;
  transform: scale(1.05);
}

/* ========= MESSAGES ========= */
.message {
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 10px;
  font-weight: 500;
  font-size: 0.95rem;
  text-align: center;
  animation: fadeInSlide 0.6s ease-in-out;
}
.message.succes {
  background-color: #ecfdf5;
  border: 1.5px solid #10b981;
  color: #065f46;
}
.message.erreur {
  background-color: #fee2e2;
  border: 1.5px solid #ef4444;
  color: #991b1b;
}

/* ========= TABLEAU ========= */
table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin-top: 2rem;
  background-color: #ffffff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.06);
  font-size: 0.95rem;
  opacity: 0;
  animation: fadeInSlide 1.2s ease-in-out forwards;
}
thead {
  background: linear-gradient(to right, #1e40af, #3b82f6);
  color: white;
}
th, td {
  padding: 1rem 0.75rem;
  text-align: center;
  border-bottom: 1px solid #e2e8f0;
}
tr:last-child td {
  border-bottom: none;
}
tbody tr:nth-child(even) {
  background-color: #f8fafc;
}
tbody tr:hover {
  background-color: #e0f2fe;
  transition: background-color 0.3s ease;
}

/* ========= BOUTONS DANS TABLEAU ========= */
td button {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  margin: 0 0.25rem;
  cursor: pointer;
  transition: all 0.25s ease;
}
td button:nth-child(1) {
  background-color: #facc15;
  color: #1e293b;
}
td button:nth-child(1):hover {
  background-color: #eab308;
}
td button:nth-child(2) {
  background-color: #ef4444;
  color: white;
}
td button:nth-child(2):hover {
  background-color: #dc2626;
}
td button:nth-child(3) {
  background-color: #10b981;
  color: white;
}
td button:nth-child(3):hover {
  background-color: #059669;
}

/* ========= BOUTON HISTORIQUE ========= */
button.historique {
  display: block;
  margin: 2rem auto 0;
  background: linear-gradient(to right, #3b82f6, #2563eb);
  color: white;
  padding: 0.85rem 1.7rem;
  border-radius: 12px;
  border: none;
  font-weight: bold;
  font-size: 1.05rem;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}
button.historique:hover {
  background-color: #1e40af;
  transform: scale(1.05);
}

/* ========= RESPONSIVE ========= */
@media (max-width: 768px) {
  form, table {
    padding: 1rem;
    font-size: 0.9rem;
  }
  form button {
    width: 100%;
    margin-bottom: 0.5rem;
  }
  table {
    display: block;
    overflow-x: auto;
    font-size: 0.85rem;
  }
  h1 {
    font-size: 1.8rem;
  }
}
